<template>

  <div class="container-lg">

    <div class="accordion" id="accordionPanelsStayExample">

      <!-- Question -->
      <h2 hidden>{{state.qid = $route.params.id }}</h2>
      <div class="accordion-item question" >
        <div hidden> CircleId that I Joined :: {{state.circleid}} </div>
        <h2 class="accordion-header" v-bind:id="'panelsStayOpen-heading'">
          <button class="accordion-button " type="button" data-bs-toggle="collapse" v-bind:data-bs-target="'#panelsStayOpen-collapse'" aria-expanded="true" v-bind:aria-controls="'panelsStayOpen-collapse'">
            {{state.title}}
          </button>
        </h2>

        <div v-bind:id="'panelsStayOpen-collapse'" class="accordion-collapse" v-bind:aria-labelledby="'panelsStayOpen-heading'">

          <div class="buttongoup" v-if="state.uid == state.registCheckuid ">
            <el-button-group class="ml-4">
              <el-button class="titlebtn" type="primary" plain data-bs-toggle="modal" data-bs-target="#staticBackdrop1" @click="sendId(state.qid, state.nickname, state.title, state.content)"> <span> 질문 수정 </span></el-button>
              <el-button class="titlebtn" type="primary" plain  @click="deleteModal"> <span> 삭제 </span> </el-button>
            </el-button-group>
          </div>



          <!-- <div class="switch">
            <label class="publiclabel"> 공개여부 ::  </label>
            <el-switch  v-model="state.questionPublic" active-text="public" inactive-text="private"  disabled />
          </div> -->

          <div class="accordion-body">
            <p><b>질문 작성자 :: </b> {{ state.nickname }} </p>
            <p><b>질문 내용 :: </b>{{ state.content }} </p>
            <p><b>수정 일자 :: </b>{{formatDate(state.modDate)}} </p>
          </div>

          <!-- Answer -->
          <div class="answerList">
            <!--PrivateAnswerRead로 넘겨주면 read용 question이 같이 보이기 때문에 Answer로 넘겨줌 -->
            <Answer
              :questionid= "state.qid"
              :qnickname="state.nickname"
              :quid="state.uid"
              />
          </div>

        </div>

      </div> <!--accordion-item question-->
    </div> <!--arccodian-->

    <!-- Modal -->
    <div class="modal fade" id="staticBackdrop1" data-bs-backdrop="static" data-bs-keyboard="false" tabindex="-1" aria-labelledby="staticBackdropLabel" aria-hidden="true">
      <div class="modal-dialog">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title" id="staticBackdropLabel">질문 수정하기</h5>
            <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
          </div>

          <div class="modal-body container">

            <div class="item modalswitch">
              <label for="nickname"> 공개여부 :: </label><br>
              <el-switch v-model="questionPublic" size="large" active-text="Public" />
            </div>

            <div class="item">
              <label for="nickname"> Nickname :: </label><br>
              <input type="text" id="nickname" v-model="modal.nickname" disabled>
            </div>

            <div class="item">
              <label for="title"> Title :: </label><br>
              <input type="text" id="title" v-model="modal.title" >
            </div>

            <div class="item">
              <label for="content"> Content :: </label><br>
              <textarea type="text" id="content" v-model="modal.content" ></textarea>
            </div>
          </div>

          <div class="modal-footer">
            <button type="button" class="btn btn-secondary" data-bs-dismiss="modal">Cancel</button>
            <button type="button" class="btn btn-primary"  @click="modifyQuestion()" data-bs-dismiss="modal">Modify</button>
          </div>
        </div>
      </div>
    </div>


    <!-- Register -->
    <Teleport to="body">
      <!-- use the modal component, pass in the prop -->
      <modal :show="state.showModal" @close="state.showModal=false">
        <template #header>
          <h3>Register Question</h3>
        </template>
        <template #body>

          <div class="item modalswitch">
            <label for=""> 공개여부 :: </label><br>
            <el-switch size="large"  active-text="Public" v-model="questionPublic"/>
          </div>

          <div class="item">
            <label for=""> Nickname :: </label><br>
            <input type="text" v-model="state.nickname" disabled>
          </div>

          <div class="item">
            <label for="title"> Title :: </label><br>
            <input type="text" id="title" v-model="regist.registTitle" style="width:400px;" >
          </div>

          <div class="item">
            <label for="content"> Content :: </label><br>
            <textarea type="text" id="content" v-model="regist.registContent"></textarea>
          </div>

        </template>
        <template #footer>
          <el-button type="secondary" plain @click="state.showModal=false"> Close </el-button>
          <el-button type="primary" plain @click="questionRegister()">Register</el-button>
        </template>
      </modal>
    </Teleport>

  </div>



</template>


<script>
  import { ElMessage, ElMessageBox } from 'element-plus'
  import { onMounted, reactive, ref } from 'vue'
  import {useRouter} from 'vue-router';
  import Modal from './teleport/Modal.vue'
  import axios from 'axios'
  import dayjs from 'dayjs'
  import Answer from './Answer.vue'



  export default {
  name: "TheQuestion",
  components:{ Answer, Modal},
  setup(){

    onMounted(() => { loginHandler(), registerUid() })

    const questionPublic = ref(true)
    const router=useRouter();

    const state = reactive({
      registCheckuid:'',
      qid: '',
      uid:'',
      circleid:'',
      nickname:'',
      title:'',
      content:'',
      questionPublic:'',
      modDate: '',
      email: sessionStorage.getItem('email'),
      token: sessionStorage.getItem('TOKEN'),
    });

    const formatDate = (dateString) => {
            const date = dayjs(dateString);
            return date.format('YYYY-MM-D HH:mm');
    };

    const modal = reactive({
      questionid:'',
      nickname:'',
      title:'',
      content:''
    })

    const sendId = (questionid, nickname, title, content) =>{
      modal.questionid = questionid
      modal.nickname = nickname
      modal.title = title
      modal.content = content
    };

    //delete Modal
    const deleteModal = () => {
      ElMessageBox.confirm(
        'Do you really want to delete your Question?',
        'Warning',
        {
          confirmButtonText: 'OK',
          cancelButtonText: 'Cancel',
          type: 'warning',
        }
      )
        .then(() => {
          ElMessage({
            type: 'success',
            message: 'Delete completed',
          })
          deleteQuestion()
        })
        .catch(() => {
          ElMessage({
            type: 'info',
            message: 'Delete canceled',
          })
        })
      }


    const loginHandler = async () => {
      const url= "http://43.201.60.125/api/questionread"

    const headers = {
      //TODO send the right amound of tokent and receive the same...
      'Content-Type' : 'application/json',
      Authorization: state.token,
      token: state.token }
      const body = { questionid: state.qid }
      await axios.post(url, body, { headers }).then(function (res) {
        if(res.status===200){
          state.circleid = res.data.dto.circleid
          state.nickname = res.data.dto.nickname
          state.title = res.data.dto.title
          state.content = res.data.dto.content
          state.questionPublic = res.data.dto.questionPublic
          state.modDate = res.data.dto.modDate
          state.uid = res.data.dto.uid
        } else {
          alert('bad.')
        }
      })
    }

    const registerUid = async () => {   // 글 등록시 로그인한 사용자가 맞는지 확인용
      const url = "http://43.201.60.125/api/questionregiuid"
      const headers = {
        //TODO send the right amound of tokent and receive the same...
        'Content-Type': 'application/json',
        Authorization: state.token,
        token: state.token,
        'name': 'test list'
      }
      const body = { email: state.email }
      await axios.post(url, body, { headers }).then(function (res) {
        if (res.status == 200) {
          state.registCheckuid = res.data
        } else {
          alert('bad.')
        }
      })
    }

    const deleteQuestion = async()=>{
    const url ='http://43.201.60.125/api/deleteQuestion'
    const headers = {
        "Content-Type" : "application/json",
        Authorization: state.token,
        "token": state.token }
        const body ={questionid : state.qid,}
        // console.log("delete Question... launch" +state.qid)
    await axios.post(url, body, {headers}).then(function(res){
      if(res.status === 200){
        router.go(0);
      }
    })
  }

  const modifyQuestion = async() =>{
      if(state.title ===''){
        alert("Please input title");title.value.focus();return false;
      }
      if(state.content ===''){
        alert("Please input content");content.value.focus();return false;
      }
      const url = "http://43.201.60.125/api/questionModify"
      const headers = {
        "Content-Type" : "application/json",
        Authorization: state.token,
        "token": state.token,
      }
      const body = {
        questionid: modal.questionid,
        nickname: modal.nickname,
        title : modal.title,
        content : modal.content,
        questionPublic : questionPublic.value,
        modDate: state.modDate
      }
      const response = await axios.post(url, body, {headers})
      if(response.status ===200){
        alert('modify successfully')
        setTimeout( loginHandler(), 1000)
      } else{
        alert('fail')
      }
    }

    const regist = reactive({
      registContent:'',
      registTitle:''
    });

    const questionRegister = async() => {
        if(regist.registContent === ''){
          alert('Please Input your content');email.value.focus(); return false;
        }
        const url = 'http://43.201.60.125/api/questionRegister'
        const headers = {
          "Content-Type" : "application/json",
          Authorization: state.token,
          "token": state.token
        }
        const body = {
          questionid : state.qid,
          uid : state.uid,
          circleid : state.circleid,
          title : regist.registTitle,
          content : regist.registContent,
          questionPublic : questionPublic.value,
        }
        const response = await axios.post(url, body, {headers})
        if(response.status === 200){
          alert('register successfully');
          state.showModal=false
          setTimeout( loginHandler(), 1000)
        } else {
          alert('fail')
        }
      }

    return{ questionPublic, sendId, modal, formatDate, registerUid, deleteModal, modifyQuestion, state, loginHandler, deleteQuestion, regist, questionRegister }

  }

}


</script>

<style scoped>

.btnreg{
  display: block;
  text-align: right;
  margin:10px;
}
.accordion-button {
  background: rgba(6, 90, 92, 0.2);
  color: rgba(0,0,0,0.8)
}

.accordion-button {
  background: rgba(6, 90, 92, 0.2);
  color: rgba(0,0,0,0.8)
}
.accordion-button:hover{
  box-shadow: 0px 6px 10px rgba(0, 0, 0, 0.4);
  transform: scale(1.01);
}
.accordion-button:focus{
  box-shadow: 0px 6px 10px rgba(6, 90, 92, 0.25);
  transition: all 0.2s;
}

.accordion-body{
  display: block;
  text-align: left;
  margin-top: 20px;
}

.buttongoup {
  display: block;
  margin: 10px;
  margin-top: 10px;
  text-align: right;
}

.buttongoup span{
  font-size: 15px;
}

.titlebtn {
  color:rgba(6, 90, 92, 0.9);
  background: none;
  border: solid 1px rgba(0,0,0,0.1);
}

.titlebtn:hover{
  background: rgba(25, 135, 84, 0.1);
}

.answer_collapse{
  border: solid 1px grey;
  padding: 0 10px;
  margin: 10px 0;
}

.answer{
  display: flex;
}

.counter{
    display:  flex;
    align-items: center;
    height: 80px;
    margin-right: 30px;
    background-color:lightgray;
    padding: 0 10px;
    border-radius: 30%;
  }

  .num{
    font-size: 30px;
    color: rgb(6, 90, 92);
    font-weight: bold;
    margin: 10px;
  }

  button {
    font-size: 20px;
    font-weight: bold;
  }

  .switch{
    display: block;
    text-align: right;
    margin-right: 10px;
  }

  .publiclabel {
    margin: 10px;
  }

  .container{
    display: block;
    text-align: left;
    width: 500px;
    height: 500px;
  }

  label{ margin:10px 0; }
  textarea{
    width:400px;
    height: 180px;
  }


</style>